import { Component } from '@angular/core';
import { DiaDto } from '../../Clases/ClasesDto'

/**
 * Generated class for the NetstoreCalendarioComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'netstore-calendario',
  templateUrl: 'netstore-calendario.html'
})
export class NetstoreCalendarioComponent {

  text: string;


  constructor() {
    console.log('Hello NetstoreCalendarioComponent Component');
    this.text = 'Hello World';

  }

  cal_etiqueta_dias = ['Dom', 'Lun', 'Mar', 'Mie', 'Jue', 'Vie', 'Sab'];
  cal_etiqueta_meses = ['Enero', 'Febrero', 'Marzo', 'Abril',
    'Mayo', 'Junio', 'Julio', 'Agosto', 'Septiembre',
    'Octubre', 'Noviembre', 'Diciembre'];
  mes = 11;
  año = 2017;
  // these are the diass of the week for each month, in order
  cal_dias_del_mes = [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31];

  // this is the current date
  cal_fecha_actual = new Date();
  cal_ArrayDeDias = new Array(7);

  Calendario(month, year) {
    this.mes = (isNaN(month) || month == null) ? this.cal_fecha_actual.getMonth() : month;
    this.año = (isNaN(year) || year == null) ? this.cal_fecha_actual.getFullYear() : year;
  }

  //Generates the needed HTML for the calendar
  generarHTML() {
    // get first dias of month
    var primerDiaDelMes = new Date(this.año, this.mes, 1);
    var PrimerDiaDelMesEnlaSemana = primerDiaDelMes.getDay();
    // find number of diass in month
    var cantidadDeDiasDelMes:number = this.obtenerCantidadDiasDelMes(this.mes);
    // Fill in month and year for header
    var nombreDelMes = this.cal_etiqueta_meses[this.mes];
    document.getElementsByClassName("headerCalendrio")[0].innerHTML = nombreDelMes + "&nbsp;" + this.año;

    var dia = 1;
    this.cal_ArrayDeDias = []
    for (var j = 0; j < 9; j++) {
      console.log(j);
      this.cal_ArrayDeDias[j] = new Array<DiaDto>(7);
      // this loop is for weekdiass (cells)
      for (var k = 0; k <= 6; k++) {
        if (dia <= cantidadDeDiasDelMes && (j > 0 || k >= PrimerDiaDelMesEnlaSemana)) {
          let datosdia = new DiaDto();
          let FechaEnstring = new String(this.año);
          let MesEnString = this.mes.toString()
          let DiaEnString = dia.toString()
          if (this.mes < 10) {
            MesEnString = '0' + MesEnString;
          };
          if (dia < 10) {
            DiaEnString = '0' + DiaEnString;
          };

          datosdia.DiaCorto = dia
          datosdia.FechaCompleta = new Date(this.año, this.mes, dia)
          datosdia.FechaCorta = this.año + '/' + this.mes + '/' + dia;
          datosdia.FechaNumerica = Number(FechaEnstring.concat(MesEnString, DiaEnString));
          this.cal_ArrayDeDias[j][k] = datosdia;
          dia++;
        }
      }
      // stop making rows if we've run out of diass
      if (dia > cantidadDeDiasDelMes) {
        break;
      }
    }
    //Write the diass to the screen

  }
  obtenerCantidadDiasDelMes(Mes: number): number {
    let cantidadDeDiasDelMes = this.cal_dias_del_mes[Mes];
    if (Mes == 1) { // Febrero Solamente!
      if ((this.año % 4 == 0 && this.año % 100 != 0) || this.año % 400 == 0) {
        cantidadDeDiasDelMes = 29;
      }
    }
    return cantidadDeDiasDelMes;
  }

  dibujarCalendario() {
    //Generates the HTML and write HTML to screen
    this.generarHTML();
  }

  Hoy() {
    this.Calendario(null, null);
    this.dibujarCalendario();
  }
  MesAnterior = function () {
    //Set the month back by one
    this.mes = (this.mes != 0) ? this.mes - 1 : 11;
    this.año = (this.mes != 11) ? this.año : this.año - 1;
    this.dibujarCalendario();
  }

  MesSiguiente = function () {
    //Set the month forward by one
    this.mes = (this.mes != 11) ? this.mes + 1 : 0;
    this.año = (this.mes != 0) ? this.año : this.año + 1;
    this.dibujarCalendario();
  }
  seleccionoDia = function (dia) {
    if (dia != null)
      alert(dia.DiaCorto)

  }

}



